/**
 * Breakpoints
 */
.c-pagination {
  max-width: 1282px !important;
  margin: 0 auto;
  padding: 0 41px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  width: 100%;
}
@media (max-width: 1080px) {
  .c-pagination {
    max-width: 992px;
    padding: 0 30px;
  }
}
@media (max-width: 992px) {
  .c-pagination {
    max-width: initial;
  }
}
.c-pagination__filters {
  width: calc(100% - 200px);
  display: flex;
  flex-flow: row wrap;
}
@media (max-width: 768px) {
  .c-pagination__filters {
    width: 100%;
    justify-content: center;
  }
}
.c-pagination__filter {
  position: relative;
  max-width: max-content;
  width: 100%;
  margin-right: 20px;
}
@media (max-width: 576px) {
  .c-pagination__filter {
    margin-right: initial;
    max-width: 100%;
    width: 100%;
    margin-bottom: 20px;
  }
}
.c-pagination__filter:last-child {
  margin: initial;
}
.c-pagination__filter-current {
  display: block;
  padding: 10px;
  border: 1px solid black;
  cursor: pointer;
}
.c-pagination__filter-current svg {
  margin-left: 8px;
}
.c-pagination__filter-options {
  display: block;
  width: 100%;
  max-height: 0px;
  transition: 0.3s all;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 89px;
  background-color: white;
  border: 1px solid black;
  border-top: initial;
  padding: 0 10px;
}
.c-pagination__filter-options.open {
  max-height: 1000px;
}
.c-pagination__filter-option {
  padding: 10px 0;
  cursor: pointer;
}
.c-pagination__search {
  width: 200px;
}
@media (max-width: 768px) {
  .c-pagination__search {
    width: 100%;
    order: -1;
    margin-bottom: 20px;
  }
}
.c-pagination__search input {
  width: 100%;
  padding: 10px;
}
.c-pagination__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin: 30px 0;
  width: 100%;
}
.c-pagination__container.loading {
  display: block;
  width: 100%;
}
.c-pagination__container.nothing-found {
  display: block;
  width: 100%;
}
@media (max-width: 1080px) {
  .c-pagination__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .c-pagination__container {
    grid-template-columns: repeat(1, 1fr);
  }
}
.c-pagination__controllers {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}
.c-pagination__controllers .prev-page,
.c-pagination__controllers .next-page {
  background-color: transparent;
  border: initial;
  font-size: 15px;
  cursor: pointer;
  transition: 0.3s all;
}
.c-pagination__controllers .prev-page:hover,
.c-pagination__controllers .next-page:hover {
  color: lightblue;
}
@media (max-width: 576px) {
  .c-pagination__controllers .prev-page,
.c-pagination__controllers .next-page {
    font-size: 13px;
  }
  .c-pagination__controllers .prev-page:hover,
.c-pagination__controllers .next-page:hover {
    color: initial;
  }
}
.c-pagination__controllers .prev-page.disabled,
.c-pagination__controllers .next-page.disabled {
  display: none;
}
.c-pagination__controllers .next-page {
  margin-left: 10px;
}
.c-pagination__controllers .prev-page {
  margin-right: 10px;
}
.c-pagination__pages {
  display: flex;
  flex-flow: row wrap;
}
.c-pagination__pages.has-prev-hidden-buttons::before {
  content: "...";
  font-size: 15px;
  line-height: 1em;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  letter-spacing: 3px;
}
@media (max-width: 576px) {
  .c-pagination__pages.has-prev-hidden-buttons::before {
    font-size: 12px;
  }
}
.c-pagination__pages.has-next-hidden-buttons::after {
  content: "...";
  font-size: 15px;
  line-height: 1em;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  letter-spacing: 3px;
}
@media (max-width: 576px) {
  .c-pagination__pages.has-next-hidden-buttons::after {
    font-size: 12px;
  }
}
.c-pagination__pages .page {
  width: 40px;
  height: 40px;
  background-color: lightgrey;
  color: black;
  border: initial;
  outline: initial;
  cursor: pointer;
  margin-right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 1em;
}
@media (max-width: 576px) {
  .c-pagination__pages .page {
    font-size: 12px;
    width: 30px;
    height: 30px;
    margin-right: 3px;
  }
}
.c-pagination__pages .page:last-child {
  margin-right: initial;
}
.c-pagination__pages .page.active {
  background-color: lightgreen !important;
  color: white !important;
}
.c-pagination__pages .page.hidden {
  display: none;
}

.c-post {
  border: 1px solid black;
  background-color: white;
  align-self: flex-start;
}
.c-post a {
  padding: 30px 15px;
  display: block;
}
.c-post__thumbnail {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: center;
  margin-bottom: 20px;
}
.c-post__title {
  font-size: 20px;
}
.c-post__brief {
  font-size: 15px;
  margin-top: 10px;
}